/* ContactPage.css - Updated to match dashboard style */

/* Root variables for design */
:root {
  --contact-bg: #f9f9f9;
  --card-bg: white;
  --card-shadow: 0 4px 8px rgba(0,0,0,0.1);
  --primary-color: #1a7431;
  --text-color: #1a2930;
}

/* Main contact section styling */
.contact-section {
  padding: 40px 20px;
  max-width: 1200px;
  margin: 0 auto;
  background: var(--contact-bg);
}

.contact-section h1 {
  margin-bottom: 30px;
  color: var(--primary-color);
  display: flex;
  align-items: center;
  font-size: 2rem;
  font-weight: 600;
}

.contact-section h1 i {
  margin-right: 15px;
}

/* Contact information cards */
.contact-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.contact-card {
  background: var(--card-bg);
  border-radius: 8px;
  padding: 25px;
  box-shadow: var(--card-shadow);
  text-align: center;
  transition: transform 0.2s, box-shadow 0.2s;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0,0,0,0.15);
}

.contact-card i {
  font-size: 2.5rem;
  margin-bottom: 15px;
}

.contact-card:nth-child(1) i {
  color: #4caf50; /* Green for location */
}

.contact-card:nth-child(2) i {
  color: #2196f3; /* Blue for phone */
}

.contact-card:nth-child(3) i {
  color: #ff9800; /* Orange for email */
}

.contact-card h3 {
  font-size: 1.3rem;
  margin-bottom: 10px;
  color: var(--text-color);
}

.contact-card p {
  color: #666;
  line-height: 1.5;
}

/* Contact form */
.contact-form {
  background: var(--card-bg);
  border-radius: 8px;
  padding: 30px;
  box-shadow: var(--card-shadow);
}

.contact-form h2 {
  color: var(--primary-color);
  margin-bottom: 25px;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 500;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #555;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.3s;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px rgba(26, 116, 49, 0.2);
}

.submit-btn {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  margin: 0 auto;
  display: block;
  width: 200px;
  transition: background-color 0.3s;
}

.submit-btn:hover {
  background-color: #135e27;
}

/* Alert styling */
.alert {
  padding: 15px;
  border-radius: 4px;
  margin-bottom: 20px;
  font-weight: 500;
}

.alert.success {
  background-color: rgba(76, 175, 80, 0.2);
  color: #2e7d32;
  border-left: 4px solid #2e7d32;
}

.alert.error {
  background-color: rgba(244, 67, 54, 0.2);
  color: #c62828;
  border-left: 4px solid #c62828;
}

/* Loading indicator */
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  font-size: 1.2rem;
  color: var(--primary-color);
}

/* Content wrapper */
.content-wrapper {
  padding: 20px;
  background-color: var(--contact-bg);
  min-height: calc(100vh - 60px); /* Adjust based on header height */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .contact-info {
    grid-template-columns: 1fr;
  }
  
  .contact-form {
    padding: 20px;
  }
  
  .submit-btn {
    width: 100%;
  }
}