/* Updated design for History page */
.history-container {
  padding: 80px 20px 20px;
  max-width: 1200px;
  margin: 0 auto;
  background: #fafafa;
}

.history-container h1 {
  margin-top: 50px;
  color: var(--primary-color);
  margin-bottom: 20px;
  text-align: center;
}

.history-table-container {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  overflow-x: auto;
  margin-top: 1rem;
}

.history-table {
  width: 100%;
  border-collapse: collapse;
  min-width: 800px;
}

.history-table th {
  background-color: var(--primary-color);
  color: white;
  padding: 12px;
  text-align: left;
}

.history-table td {
  padding: 12px;
  border-bottom: 1px solid #eee;
}

.history-table tr:hover {
  background-color: #f0f0f0;
}

/* States and alerts */
.loading {
  text-align: center;
  padding: 20px;
  color: var(--text-secondary);
}

.error-message {
  background-color: #ffebee;
  color: #c62828;
  padding: 12px;
  border-radius: 4px;
  margin-bottom: 20px;
  text-align: center;
}

.status {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  text-transform: capitalize;
}

.status.pending {
  background-color: #fff3e0;
  color: var(--warning-color);
}

.status.completed {
  background-color: #e8f5e9;
  color: var(--primary-color);
}

.status.rejected {
  background-color: #ffebee;
  color: #c62828;
}

/* Empty state */
.empty-state {
  text-align: center;
  padding: 40px 20px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.empty-state p {
  font-size: 18px;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.empty-state small {
  color: #999;
}

/* Responsive */
@media (max-width: 768px) {
  .history-container {
    padding: 60px 10px 10px;
  }

  .history-table th,
  .history-table td {
    padding: 8px;
    font-size: 14px;
  }
}