/* src/styles/Insert.css */
/* Updated design for Insert page */
:root {
  --primary-color: #059669;
  --secondary-color: #047857;
  --accent-color: #135e27;
  --bg-color: #f4f7f6;
  --text-color: #1a2930;
  --border-color: #e0e0e0;
  --shadow: 0 4px 8px rgba(0,0,0,0.1);
}

/* Base styles */
h1 {
  font-size: clamp(1.75rem, 4vw, 2.25rem);
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 20px;
}

/* Alert styles */
.alert-success, .alert-error {
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 4px;
  font-size: clamp(14px, 2vw, 16px);
}

.alert-success {
  background-color: #e8f5e9;
  color: #388e3c;
}

.alert-error {
  background-color: #ffebee;
  color: #c62828;
}

/* Control panel and grid layout */
.control-panel {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}

.insert-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
  padding: 2rem;
  background: var(--bg-color);
}

/* Card styles */
.status-card,
.control-card,
.preview-card {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: var(--shadow);
  border: 1px solid var(--border-color);
}

/* Typography for stats */
.stat-label {
  color: var(--text-color);
  font-size: 1rem;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-value {
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

/* Button styles */
.button-group {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}

.control-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: all 0.2s ease-in-out;
}

.control-btn i {
  margin-right: 5px;
  font-size: clamp(14px, 2vw, 16px);
}

.start-btn {
  background: var(--primary-color);
  color: white;
}

.start-btn:hover:not(:disabled) {
  background: var(--secondary-color);
}

.start-btn:disabled {
  background: var(--border-color);
  cursor: not-allowed;
}

.cancel-btn {
  background: #ffebee;
  color: #c62828;
}

.cancel-btn:hover {
  background: #f44336;
  color: white;
}

.submit-btn {
  width: 100%;
  background: var(--primary-color);
  color: white;
  margin-top: 1rem;
  border-radius: 8px;
  padding: 1rem;
}

.submit-btn:hover:not(:disabled) {
  background: var(--accent-color);
}

.submit-btn:disabled {
  background: var(--border-color);
  cursor: not-allowed;
}

.timer-display {
  text-align: center;
  font-size: 1.25rem;
  color: var(--text-color);
  margin-top: 1rem;
}

/* Responsive */
@media (max-width: 768px) {
  .insert-grid {
    grid-template-columns: 1fr;
    padding: 1rem;
  }
  
  .button-group {
    flex-direction: column;
  }
}

/* Main container styles */
.main-container {
  margin-left: var(--sidebar-width);
  position: relative;
  z-index: 10; /* or another value lower than the sidebar overlay if needed */
}

@media (max-width: 768px) {
  .main-container {
    margin-left: 0;
  }
}